<template>
  <div class="row">
      <div class="col-md-3" style="position:relative;min-height:30px">
        <div class="box btn-group">
            <router-link :to="'/pro/'+id+'/index'" class="btn btn-light" >项目主页</router-link>
            <router-link :to="'/pro/'+id+'/order'" class="btn btn-light" >订单列表</router-link>
            <div class="btn-group dropdown">
                <button type="button" class="btn btn-light dropdown-toggle" data-toggle="dropdown" v-if="power==0">
                更多
                </button>
                <div class="dropdown-menu">
                <router-link :to="'/pro/'+id+'/set'" class="dropdown-item text-center">设置</router-link>
                </div>
            </div>
        </div>
      </div>
      <div class="col-md-9 user-page">
          <router-view></router-view>
      </div>
  </div>
</template>

<script>
import {checkPower,proInfo,proMembers} from '../../api/pro.js';
import {LOADING_START,LOADING_END} from '../../store/mutation_types.js';
export default{
  created(){
    this.id=this.$route.params.id;
    this.init();
  },
  data(){
    return{
      id:'',
      power:0
    }
  },
  methods:{
    async init(){
          this.$store.commit(LOADING_START)
          let res=await checkPower({id:this.id})
          if(res.data.res!=1){this.$store.commit(LOADING_END);alert(res.data.text);this.$router.push('/home');return};
          this.power=res.data.body;
          this.$store.commit(LOADING_END)
      }
  }
}

</script>

<style scoped lang="less">
@import "../../assets/css/theme.less";
@media screen and (min-width:768px){
    .box{
       flex-direction: column;
       padding-top:50px;
       .btn{
           width: 50%;
           margin: 10px auto;
       }
    }
}
@media screen and (max-width:768px){
    .box{
       flex-direction: row;
        .btn{
           margin: 0 5px;
       }
    }
}
.box{
    display: flex;
    justify-content: center;
    box-shadow: none;
    padding-bottom: 10px;
    position: absolute;
    width: calc(100% - 30px);
    .btn{
        border-radius: 5px !important;
    }
}
.user-page{
    margin-top: 20px;
}
a.router-link-active{
    background-color: @cut3;
    color:#fff;
}

</style>



